
.projects-section {
  min-height: 100vh;
  background-color: #0f0f0f;
  color: white;
  padding: 4rem 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects-title {
  font-size: 2.5rem;
  color: cyan;
  margin-bottom: 2rem;
  text-align: center;
}

.subsection-title {
  font-size: 1.8rem;
  color: #00ffff;
  margin-top: 3rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

.projects-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.project-card {
  background-color: #1a1a1a;
  border: 2px solid cyan;
  border-radius: 12px;
  padding: 1.5rem;
  width: 300px;
  text-align: center;
  transition: transform 0.3s ease;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.2);
}

.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.4);
}

.project-img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.project-link {
  display: inline-block;
  margin: 0.5rem 0.25rem;
  padding: 0.4rem 0.8rem;
  background-color: cyan;
  color: #000;
  font-weight: bold;
  text-decoration: none;
  border-radius: 6px;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.3);
  transition: all 0.3s ease;
}

.project-link:hover {
  background-color: #00ffff;
  box-shadow: 0 0 15px rgba(0, 255, 255, 0.6);
  transform: scale(1.05);
}
